              i.fas.fa-circle(style="color:#33ECE0")
              p.mb-0 <strong>Anuncios de clic a WhatsApp:</strong> conectan a los clientes potenciales presentes en plataformas como WhatsApp e Instagram, mostrando a aquellos usuarios ms propensos a entablar una conversacin con la marca o empresa.
            
    p.mb-5 Estos recursos estratgicos no solo facilitan la comunicacin, sino que tambin ofrecen oportunidades valiosas para que las empresas amplen su alcance, mejoren la interaccin con los clientes y fortalezcan su presencia en lnea.
    
    .row.justify-content-center.align-items-center.mb-5
      .col-10(data-aos="fade-down-right")
        .cajon.color-secundario.p-4(style="background-color:#FFFBE5")(data-aos="flip-up")
          .row.justify-content-center.align-items-center 
            .col-md-8.mb-3
              p <strong>Instagram</strong> se encuentra en la cuarta posicin entre las redes sociales con mayor nmero de usuarios activos, convirtindola en una plataforma atractiva para ampliar el alcance de la poblacin.

            .col-md-2
              figure
                img(src='@/assets/curso/temas/tema1/img-21.png', alt='Imagen decorativa')

    .row.mb-5
      .col-md-4.mb-3
        figure
            img(src='@/assets/curso/temas/tema1/img-22.png', alt='')
      .col-md-8
        p Quienes utilizan Instagram, Facebook y WhatsApp como parte de sus objetivos estratgicos, buscan:
        ul.lista-ul.mb-3
          li.mb-0
            i.fas.fa-circle(style="color:#33ECE0")
            p.mb-0 <strong>Aumentar el reconocimiento de la marca:</strong> utilizando estas plataformas para consolidar la presencia de su marca y aumentar la visibilidad entre los usuarios.
          li.mb-0
            i.fas.fa-circle(style="color:#33ECE0")
            p.mb-0 <strong>Promocionar su negocio local:</strong> aprovechando la geolocalizacin y las herramientas de promocin local para llegar a clientes en reas especficas.
          li.mb-0
            i.fas.fa-circle(style="color:#33ECE0")
            p.mb-0 <strong>Incrementar las ventas <em>online:</em></strong> utilizando las funciones de comercio electrnico y promociones para impulsar las ventas a travs de estas plataformas.
          li.mb-0
            i.fas.fa-circle(style="color:#33ECE0")
            p.mb-0 <strong>Promocionar su App:</strong> utilizando las redes sociales como un canal para dar a conocer y promocionar aplicaciones.
          li.mb-0
            i.fas.fa-circle(style="color:#33ECE0")
            p.mb-0 <strong>Generar clientes potenciales:</strong> utilizando estrategias de <em>marketing</em> para atraer y retener a nuevos clientes interesados en los productos o servicios ofrecidos.
          li.mb-0
            i.fas.fa-circle(style="color:#33ECE0")
            p.mb-0 <strong>Monetizar el contenido:</strong> aprovechando las oportunidades de generar ingresos a travs de contenido patrocinado, colaboraciones o ventas directas.
          li.mb-0
            i.fas.fa-circle(style="color:#33ECE0")
            p.mb-0 <strong>Redirigir a los clientes actuales:</strong> utilizando estas plataformas para mantener a los clientes actuales informados, comprometidos y redirigirlos a nuevas ofertas o productos.

    p.mb-5 Estos objetivos reflejan la versatilidad de estas plataformas sociales como herramientas estratgicas para diversos propsitos comerciales, desde el fortalecimiento de la marca hasta la generacin de ingresos y la gestin de la relacin con el cliente. Por tal razn Instagram ofrece a las empresas las siguientes herramientas: 

    .BGIMG01.p-5.mb-5
      SlyderA(tipo="b")
        .row
          .col-md-6.mb-4.mb-md-0.col-ipad
            h3 Cuentas empresariales
            p Creacin de cuentas empresariales guiada por un asistente predeterminado de Instagram. 

          .col-md-6.col-ipad
            figure
              img(src='@/assets/curso/temas/tema1/img-23.png', style="width: 450px" ,alt='').m-auto
        .row
          .col-md-6.mb-4.mb-md-0.col-ipad
            h3 Publicacin del anuncio
            p Cuando se ha tenido en cuenta todo lo anterior se procede a publicar el anuncio segn los parmetros que se plantearon, la gua detallada para esto se encuentra en el apartado servicio para ayuda de empresas dentro de Instagram.

          .col-md-6.col-ipad
            figure
              img(src='@/assets/curso/temas/tema1/img-24.png', style="width: 450px" ,alt='').m-auto
        .row
          .col-md-6.mb-4.mb-md-0.col-ipad
            h3 Tipo de anuncio
            p Instagram ofrece diversos tipos de anuncios, entre ellos estn:
            ul.lista-ul.mb-3
              li.mb-0
                i.fas.fa-circle(style="color:#000")
                | Anuncios con foto.
              li.mb-0
                i.fas.fa-circle(style="color:#000")
                | Anuncios con video.
              li.mb-0
                i.fas.fa-circle(style="color:#000")
                p.mb-0 <strong>Anuncios por secuencia:</strong> permite crear un anuncio con una serie de fotos para dar ms contenido al mismo anuncio.
              li.mb-0
                i.fas.fa-circle(style="color:#000")
                p.mb-0 <strong>Anuncios de Instagram stories:</strong> permiten la accin del usuario y el llamado a la accin como la redireccin a la pgina #[em web], a WhatsApp, mensaje al directo o la visita al perfil de la marca en Instagram.
              li.mb-0
                i.fas.fa-circle(style="color:#000")
                p.mb-0 <strong>Anuncios en la seccin de explorar:</strong> Instagram posee una opcin de bsqueda en la cual aparecen estos anuncios de seccin de explorar, con el objetivo de que los usuarios expandan sus intereses y encuentren nuevo contenido. 

          .col-md-6.col-ipad
            figure
              img(src='@/assets/curso/temas/tema1/img-25.png', style="width: 450px" ,alt='').m-auto
        .row
          .col-md-6.mb-4.mb-md-0.col-ipad
            h3 Promocin del anuncio
            p Para esta seccin se debe de tener claro el pblico al cual se quiere impactar, partiendo de los objetivos de la poblacin de inters se procede a segmentar la publicacin con la ayuda del asistente guiado de Instagram. 

          .col-md-6.col-ipad
            figure
              img(src='@/assets/curso/temas/tema1/img-26.png', style="width: 450px" ,alt='').m-auto
        .row
          .col-md-6.mb-4.mb-md-0.col-ipad
            h3 Formas para la publicacin
            p El anuncio se puede publicar de tres formas diferentes: 

            ul.lista-ul.mb-3
              li.mb-0
                i.fas.fa-circle(style="color:#000")
                p.mb-0 <strong>App de Instagram:</strong> se escoge la publicacin que se quiere promocionar, se utilizan las herramientas anteriores y se empieza el seguimiento de la circulacin del anuncio.
              li.mb-0
                i.fas.fa-circle(style="color:#000")
                p.mb-0 <strong>Administrador de anuncios:</strong> permite modificar, configurar y consultar la informacin, este recurso se encuentra disponible para celulares y PC.
              li.mb-0
                i.fas.fa-circle(style="color:#000")
                p.mb-0 <strong>Socios de Instagram:</strong> realizan toda la gestin de los anuncios (compra, creacin y entrega de contenido), Instagram verifica que los socios sean idneos para la gestin de los anuncios. 
          
          .col-md-6.col-ipad
            figure
              img(src='@/assets/curso/temas/tema1/img-27.png', style="width: 450px" ,alt='').m-auto

    p.mb-5 Adems de las redes sociales, que constituyen las comunidades virtuales ms conocidas y difundidas, existen otras plataformas que tambin desempean un papel relevante en el ciberespacio. Algunas de estas incluyen:

    TabsB.color-acento-botones(style="background-color:#F7F2F2")
      .py-4.py-md-5(titulo="<em>Blog</em>" :icono="require('@/assets/curso/temas/tema1/decorador-1.svg')")
        .row
          .col-md-5.mb-4.mb-md-0
            figure
              img(src='@/assets/curso/temas/tema1/img-28.png', alt='Texto que describa la imagen')

          .col-md-6
            h4 <em>Blog</em>
            p Son una herramienta importante de interaccin, puesto que brindan el beneficio de que la poblacin pueda interactuar mediante comentarios entre la misma poblacin virtual que visita el <em>blog,</em> as como la retroalimentacin de los comentarios.
            p Esto lo hace una estratgica herramienta de interrelacin que le permite a la entidad comercial dirigirse a una audiencia especfica (<em>buyer</em> persona) con un tema en particular. 
      
      .py-4.py-md-5(titulo="<em>Insights community</em>" :icono="require('@/assets/curso/temas/tema1/decorador-2.svg')")
        .row
          .col-md-5.mb-4.mb-md-0
            figure
              img(src='@/assets/curso/temas/tema1/img-29.png', alt='Texto que describa la imagen')

          .col-md-6
            h4 <em>Insights community</em> 
            p Son una herramienta de investigacin comercial o de mercado, en la cual se le permite a la empresa obtener informacin de un grupo de clientes que son fieles seguidores de las marcas.
            p Con el objetivo de recopilar la informacin relevante para mejorar la experiencia del cliente y poder ajustar las campaas de <em>marketing</em> para que sean ms eficientes y eficaces. 
      
      .py-4.py-md-5(titulo="Comunidades promotoras de marca" :icono="require('@/assets/curso/temas/tema1/decorador-3.svg')")
        .row
          .col-md-5.mb-4.mb-md-0
            figure
              img(src='@/assets/curso/temas/tema1/img-30.png', alt='Texto que describa la imagen')

          .col-md-6
            h4 Comunidades promotoras de marca
            p Son un gran aliado a la hora de realizar las campaas publicitarias de las entidades, puesto que son un grupo de personas apasionadas por la experiencia que les ha brindado la marca, escriben testimonios referenciando la marca y realizan este tipo de actividades a cambio de una recompensa. 
      
      .py-4.py-md-5(titulo="<em>Email y email groups</em>" :icono="require('@/assets/curso/temas/tema1/decorador-4.svg')")
        .row
          .col-md-5.mb-4.mb-md-0
            figure
              img(src='@/assets/curso/temas/tema1/img-31.png', alt='Texto que describa la imagen')

          .col-md-6
            h4 <em>Email y email groups</em>
            p Son creados con el propsito de intercambiar informacin relevante para el equipo de trabajo, en la actualidad un grupo de personas pueden trabajar en el mismo documento sin necesidad de reunirse personalmente, todo ello debido a programas como Google Drive, que permite mediante una carpeta compartida la visualizacin, el descargue y la modificacin de archivos en tiempo real, cambios que evidencian las personas con las cuales se ha compartido el documento en tiempo real.
      
      .py-4.py-md-5(titulo="Gestores de contenido" :icono="require('@/assets/curso/temas/tema1/decorador-5.svg')")
        .row
          .col-md-5.mb-4.mb-md-0
            figure
              img(src='@/assets/curso/temas/tema1/img-32.png', alt='Texto que describa la imagen')

          .col-md-6
            h4 Gestores de contenido
            p Son programas que guan en la creacin, la edicin y la publicacin de contenido, es decir, es un <em>software</em> facilitador de la gestin de contenidos de la comunidad virtual, que no requiere conocimientos avanzados en informtica.
      
      .py-4.py-md-5(titulo="<em>News groups</em>" :icono="require('@/assets/curso/temas/tema1/decorador-6.svg')")
        .row
          .col-md-5.mb-4.mb-md-0
            figure
              img(src='@/assets/curso/temas/tema1/img-33.png', alt='Texto que describa la imagen')

          .col-md-6
            h4 <em>News groups</em>
            p Son grupos creados especficamente para compartir noticias y crear interaccin directa de la empresa con la comunidad, estos grupos son moderados por un <em>community manager,</em> que media las interacciones y las mantiene presentes en el foco de discusin, en pro de aumentar la audiencia.

    Separador 

    #t_1_1.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.1 Estrategias para la creacin de comunidades virtuales

    p.mb-5 La creacin de estrategias para comunidades virtuales implica seguir pasos fundamentales que aseguren el xito del proyecto. A continuacin, se detallan los siguientes:

    .row.justify-content-center.align-items-center.mb-4 
      .col-12.col-lg-11(data-aos="fade-right")
        ImagenInfograficaB.color-acento-botones.desktop(style="min-height:551px")
          template(v-slot:imagen)
            figure
              img(src='@/assets/curso/temas/tema1/img-34-1.svg', alt='Texto que describa la imagen')

          .row.p-3(x="37.5%" y="7%" tooltip="" numero="1")
            .col-md-6.col-12.texto-negro
              h5.mb-2.texto-negro Paso 1: Preguntas iniciales
              p(style="color:#000 !important") Segn GRANTHAM, se debe perseguir el objetivo de satisfacer las necesidades psicolgicas de la comunidad virtual, para ello se requiere que se realicen las siguientes preguntas que el cliente requiere resolver:
              ul.lista-ul.mb-3(style="color:#000 !important")
                li.mb-0
                  i.fas.fa-circle(style="color:#000")
                  | Quin soy yo?
                li.mb-0
                  i.fas.fa-circle(style="color:#000")
                  | De qu hago parte?
                li.mb-0
                  i.fas.fa-circle(style="color:#000")
                  | Qu me conecta con el resto del mundo y en qu medida se relaciona con el resto   de la comunidad?
                li.mb-0
                  i.fas.fa-circle(style="color:#000")
                  | Qu recibo de los dems?
            
            .col-md-6.col-12
              figure
                img(src='@/assets/curso/temas/tema1/img-35.png', alt='Texto que describa la imagen')

          .row.p-3(x="62%" y="7%" tooltip="" numero="2")
            .col-md-6.col-12.texto-negro
              h5.mb-2.texto-negro Paso 2: Objetivo de la comunidad
              p(style="color:#000 !important") Definir el objetivo de una comunidad virtual es fundamental para su xito. Esto proporciona direccin, ayuda a identificar el pblico objetivo, permite crear contenido relevante, mejora la participacin de los miembros y proporciona informacin valiosa para desarrollar estrategias de <em>marketing</em> efectivas.  
            
            .col-md-6.col-12
              figure
                img(src='@/assets/curso/temas/tema1/img-36.png', alt='Texto que describa la imagen')

          .row.p-3(x="83%" y="19%" tooltip="" numero="3")
            .col-md-6.col-12.texto-negro
              h5.mb-2.texto-negro Paso 3: Formular el objetivo
              p(style="color:#000 !important") Para definir el objetivo es necesario realizarse la pregunta: Para qu va a incursionar en esa red social? Dentro de los objetivos que se deben plantear estn: 
              ul.lista-ul.mb-3(style="color:#000 !important")
                li.mb-0
                  i.fas.fa-circle(style="color:#000")
                  | Aumentar las visitas a la pgina <em>web.</em>
                li.mb-0
                  i.fas.fa-circle(style="color:#000")
                  | Incrementar el alcance de la publicidad de la marca.
                li.mb-0
                  i.fas.fa-circle(style="color:#000")
                  | Promocionar publicaciones. 
                li.mb-0
                  i.fas.fa-circle(style="color:#000")
                  | Captacin de clientes potenciales.
                li.mb-0
                  i.fas.fa-circle(style="color:#000")
                  | Fortalecer la relacin con los clientes potenciales.
            
            .col-md-6.col-12
              figure
                img(src='@/assets/curso/temas/tema1/img-37.png', alt='Texto que describa la imagen')

          .row.p-3(x="94%" y="42%" tooltip="" numero="4")
            .col-md-6.col-12.texto-negro
              h5.mb-2.texto-negro Paso 4: Pensar el canal
              p(style="color:#000 !important") La eleccin del canal para crear una comunidad virtual depende de los objetivos establecidos. Puede realizarse a travs de redes sociales, <em>blogs,</em> plataformas de anlisis <em>(insights)</em> o comunidades especficas de marca. La seleccin del canal debe alinearse con los objetivos y preferencias del pblico objetivo, considerando las caractersticas y ventajas de cada plataforma para lograr los resultados deseados.
            
            .col-md-6.col-12
              figure
                img(src='@/assets/curso/temas/tema1/img-38.png', alt='Texto que describa la imagen')

          .row.p-3(x="92%" y="68%" tooltip="" numero="5")
            .col-md-6.col-12.texto-negro
              h5.mb-2.texto-negro Paso 5: Canal segn objetivo 
              p(style="color:#000 !important") De acuerdo con el objetivo de la empresa, se hace necesario establecer el canal de comercio, pues no todas las redes sociales se usan para el mismo objetivo, por ejemplo, algunas redes sociales segn el objetivo de la empresa pueden ser:
              ul.lista-ul.mb-3(style="color:#000 !important")
                li.mb-0
                  i.fas.fa-circle(style="color:#000")
                  | Linkedin
                li.mb-0
                  i.fas.fa-circle(style="color:#000")
                  | X
                li.mb-0
                  i.fas.fa-circle(style="color:#000")
                  | Facebook
            
            .col-md-6.col-12
              figure
                img(src='@/assets/curso/temas/tema1/img-39.png', alt='Texto que describa la imagen')

          .row.p-3(x="74%" y="87%" tooltip="" numero="6")
            .col-md-6.col-12.texto-negro
              h5.mb-2.texto-negro Paso 6: <em>Buyer</em> persona
              p(style="color:#000 !important") Establecer el <em>buyer</em> persona es indispensable tener presente a qu pblico se va a dirigir, es necesario conocer las caractersticas, los gustos, las preferencias, entre otros, todo aquello con el objetivo de establecer la similitud entre el producto y la poblacin activa de la comunidad, teniendo en cuenta que cada red social tiene sus cdigos y reglas para la interaccin y la socializacin.
            
            .col-md-6.col-12
              figure
                img(src='@/assets/curso/temas/tema1/img-40.png', alt='Texto que describa la imagen')

          .row.p-3(x="50%" y="93%" tooltip="" numero="7")
            .col-md-6.col-12.texto-negro
              h5.mb-2.texto-negro Paso 7: Tiempo de duracin
              p(style="color:#000 !important") Programar el tiempo de duracin es crucial para la gestin efectiva de comunidades virtuales. Establecer un cronograma proporciona organizacin, seguimiento, priorizacin, planificacin de recursos, comunicacin eficaz y mejora la eficiencia general del proyecto. Es una prctica fundamental para asegurar la ejecucin adecuada de las actividades y alcanzar los objetivos establecidos.
            
            .col-md-6.col-12
              figure
                img(src='@/assets/curso/temas/tema1/img-41.png', alt='Texto que describa la imagen')

          .row.p-3(x="27%" y="87%" tooltip="" numero="8")
            .col-md-6.col-12.texto-negro
              h5.mb-2.texto-negro Paso 8: Reglas claras
              p(style="color:#000 !important") Expresar reglas claras es fundamental en la gestin de comunidades virtuales. Establecer normas respecto a la forma de interactuar es esencial para lograr los objetivos, mantener el bienestar y fomentar el respeto dentro de la comunidad. Estas reglas proporcionan un marco estructurado que gua el comportamiento de los miembros, promoviendo un ambiente positivo y productivo.
            
            .col-md-6.col-12
              figure
                img(src='@/assets/curso/temas/tema1/img-42.png', alt='Texto que describa la imagen')

          .row.p-3(x="10%" y="68%" tooltip="" numero="9")
            .col-md-6.col-12.texto-negro
              h5.mb-2.texto-negro Paso 9: Contenido de calidad
              p(style="color:#000 !important") Crear contenido de calidad con buenas promociones y de alto contenido viral. Para esta estrategia es necesario enmarcar una relacin clara entre el tiempo de contenido y los contenidos, tanto de promociones como de interaccin, es decir; las promociones que publica la marca, como las noticias de inters, que segn el nicho de mercado al que se est segmentando sean de inters.
          
            .col-md-6.col-12
              figure
                img(src='@/assets/curso/temas/tema1/img-43.png', alt='Texto que describa la imagen')
            
          .row.p-3(x="7%" y="43%" tooltip="" numero="10")
            .col-md-6.col-12.texto-negro
              h5.mb-2.texto-negro Paso 10: Comunicacin y segmentacin
              p(style="color:#000 !important") La comunicacin en una comunidad virtual debe ser adecuada y humana, enfocndose en mantener un flujo constante de interaccin. Es crucial segmentar la poblacin para adaptar el mensaje a las necesidades especficas de cada grupo, facilitando as una comunicacin ms efectiva y personalizada. Este enfoque contribuye al fortalecimiento de la conexin entre los miembros de la comunidad y promueve un ambiente de intercambio de informacin y experiencias de manera ms significativa.
            
            .col-md-6.col-12
              figure
                img(src='@/assets/curso/temas/tema1/img-44.png', alt='Texto que describa la imagen')

          .row.p-3(x="17%" y="20%" tooltip="" numero="11")
            .col-md-6.col-12.texto-negro
              h5.mb-2.texto-negro(style="color:#000 !important") Paso 11: Medicin
              p(style="color:#000 !important") Establecer herramientas de medicin en la gestin de comunidades virtuales, especialmente en redes sociales, es crucial. La medicin del contenido e interacciones es esencial para evaluar la efectividad de la estrategia, ajustarla segn los resultados, identificar tendencias, calcular el retorno de inversin (ROI), optimizar el contenido y presentar informes que demuestren el valor y el impacto de las actividades.
            
            .col-md-6.col-12
              figure
                img(src='@/assets/curso/temas/tema1/img-45.png', alt='Texto que describa la imagen')

    .row.justify-content-center.align-items-center.mb-3
      .col-10.col-md-10.col-lg-12.movil(data-aos="fade-right")
        a.anexo.mb-4(:href="obtenerLink('/downloads/Infografia_Estrategia_para_crear_comunidades_virtuales.pdf')" target="_blank")
          .anexo__icono
            img(src="@/assets/template/icono-pdf.svg")
          .anexo__texto
            p <strong>Descargar. Infografa.</strong> Estrategia para crear comunidades virtuales

    p.mb-5 La implementacin efectiva de estas estrategias garantiza la construccin y el mantenimiento exitoso de una comunidad virtual, cumpliendo con los objetivos establecidos y proporcionando un valor sostenible a sus miembros.

    Separador 

    #t_1_2.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.2 Posicionamiento y crecimiento de la comunidad virtual

    p.mb-5 La comunidad requiere de interaccin e intercambio de experiencias, ideas y opiniones que complementen su conocimiento, por lo que la comunicacin a travs de las redes sociales permite que se generen espacios de colaboracin o de discusin. 

    .row.mb-5
      .col-md-4.mb-3
        figure
            img(src='@/assets/curso/temas/tema1/img-46.png', alt='')
      .col-md-8
        .row
          .col-lg-12
            .cajon.color-secundario.p-4.mb-4(style="background-color:#FFFBE5")
              p De acuerdo con lo anterior, para el posicionamiento y crecimiento de la comunidad virtual se hace necesario un manejo adecuado en las salas de comunidad virtual, este manejo se encontrar a cargo de los <em>community manager</em> quienes mediante tcticas sistemticas, estrategias de <em>marketing</em> como la moderacin de la comunicacin al interior de los <em>blog,</em> la escucha activa de los seguidores, el anlisis de las necesidades del mercado, la identificacin de oportunidades para implementar nuevas tendencias que favorezcan el posicionamiento de la marca y aumenten su <em>goodwill.</em> 

        p Para lograr el objetivo de posicionamiento y crecimiento de la comunidad virtual se puede optar por mecanismos que impulsen la interaccin en el ciberespacio, tales como:
        ul.lista-ul.mb-3
          li.mb-0
            i.fas.fa-circle(style="color:#33ECE0")
            p.mb-0 <em>Social media marketing.</em>
          li.mb-0
            i.fas.fa-circle(style="color:#33ECE0")
            p.mb-0 <em>Web 2.0.</em>
          li.mb-0
            i.fas.fa-circle(style="color:#33ECE0")
            p.mb-0 <em>Fanpage.</em>
          li.mb-0
            i.fas.fa-circle(style="color:#33ECE0")
            p.mb-0 <em>Engagement.</em>
        p El ROI <em>(Return on investment)</em> y el ROAS <em>(Return On Advertising Spend)</em> son mtricas de clculo matemtico que le permitir a la entidad calcular el retorno de la inversin, los canales de comunicacin ms eficaces, el seguimiento al proceso de ventas, la entrada de dinero ms fuerte de la entidad, entre otros.

    .row.justify-content-center.align-items-center.mb-5
      .col-10(data-aos="fade-down-right")
        .cajon.color-primario.p-4(style="background-color:#E3ECF7")(data-aos="flip-up")
          .row.justify-content-center.align-items-center 
            .col-md-8.mb-3
              p.mb-4 Es importante tener claridad en las mtricas para evaluacin de resultados, calculando el ROI y el ROAS, como se muestra a continuacin:
              
              div.text-center.mb-4(data-aos="fade-right")
                mfrac
                  mi.italic ROI<sub></sub> =&nbsp;
                math.text-center
                  mfrac
                    msup
                      mi.italic Beneficio - Inversin
                    msup
                      mi.italic Inversin
                mfrac
                  mi.italic X<sub></sub> 100&nbsp;

              p Esta es una frmula para calcular el retorno de la inversin, es decir, cunto dinero ha ingresado despus de invertir en la campaa publicitaria.  

            .col-md-3
              figure
                img(src='@/assets/curso/temas/tema1/decorador-7.svg', alt='Imagen decorativa')

    .row.justify-content-center.align-items-center.mb-5
      .col-8(data-aos="fade-down-right")
        p Ahora, un ejemplo prctico del clculo del <strong>ROI</strong> en campaas publicitarias: 
        p.mb-4 La empresa ha gastado $200.000 en campaas publicitarias, de las cuales se obtuvieron ingresos de $350.000 tras la finalizacin de la campaa, entonces, se aplica la frmula:
        
        .row.justify-content-center.align-items-center.mb-4
          .col-8(data-aos="fade-down-right")
            .cajon.color-secundario.p-4(style="background-color:#FFFBE5")(data-aos="flip-up")
              .row.justify-content-center.align-items-center 
                .col-lg-4.mb-3
                  figure(data-aos="fade-right")
                    img(src='@/assets/curso/temas/tema1/img-47.png', alt='Texto que describa la imagen')

                .col-lg-8
                  div.mb-3(data-aos="fade-right")
                    mfrac
                      mi.italic ROI<sub></sub> =&nbsp;
                    math.text-center
                      mfrac
                        msup
                          mi.italic $350.000 - $200.000
                        msup
                          mi.italic $200.000
                    mfrac
                      mi.italic X<sub></sub> 100&nbsp;

                  div(data-aos="fade-right")
                    mfrac
                      mi.italic ROI<sub></sub> = 75 %&nbsp;

        p <strong>Interpretacin:</strong> un ROI del 75 % indica que, por cada peso invertido en la campaa publicitaria, la empresa obtuvo un retorno del 75 %. Expresado como porcentaje, significa que la inversin gener un 75 % de ganancia adicional sobre el costo inicial de la campaa. Este resultado sugiere que la campaa fue rentable y gener un retorno positivo para la empresa.

    .row.justify-content-center.align-items-center.mb-5
      .col-10(data-aos="fade-down-right")
        .cajon.color-primario.p-4(style="background-color:#E3ECF7")(data-aos="flip-up")
          .row.justify-content-center.align-items-center 
            .col-lg-12
              p.mb-4 Ahora, es importante calcular el <strong>ROAS</strong> para medir el retorno de la inversin especficamente relacionado con gastos publicitarios y evaluar la eficacia de las campaas de publicidad y su impacto en los resultados comerciales. La frmula del ROAS es:

              div.text-center.mb-3(data-aos="fade-right")
                mfrac
                  mi.italic ROAS<sub></sub> =&nbsp;
                math.text-center
                  mfrac
                    msup
                      mi.italic Ingreso por ventas
                    msup
                      mi.italic Inversin publicitaria
              
    .row.mb-5
      .col-md-4.mb-3
        figure
            img(src='@/assets/curso/temas/tema1/img-48.png', alt='')
      .col-md-8
        p A continuacin, un ejemplo prctico del clculo del <strong>ROAS</strong> en campaas publicitarias: 
        p.mb-3 La empresa ha gastado $350.000 en campaas publicitarias, de las cuales se obtuvieron ingresos de $750.000 tras la finalizacin de la campaa.
        
        .row.justify-content-center.align-items-center.mb-4
          .col-8(data-aos="fade-down-right")
            .cajon.color-secundario.p-4(style="background-color:#FFFBE5")(data-aos="flip-up")
              .row.justify-content-center.align-items-center 
                .col-lg-2.mb-3
                  figure(data-aos="fade-right")
                    img(src='@/assets/curso/temas/tema1/img-49.svg', alt='Texto que describa la imagen')

                .col-lg-8
                  div.mb-3(data-aos="fade-right")
                    mfrac
                      mi.italic ROAS<sub></sub> =&nbsp;
                    math.text-center
                      mfrac
                        msup
                          mi.italic $750.000
                        msup
                          mi.italic $350.000

                  div(data-aos="fade-right")
                    mfrac
                      mi.italic ROAS<sub></sub> = $2.14&nbsp;

        p <strong>Interpretacin:</strong> un ROAS de 2.14 significa que, por cada peso invertido en la campaa publicitaria, la empresa gener $2.14 en ingresos. Este resultado indica que la campaa fue rentable, ya que el retorno es ms de dos veces el gasto invertido. Un ROAS superior a 1 sugiere que la campaa ha sido efectiva en generar ingresos en comparacin con el costo de la publicidad.

    Separador 

    #t_1_3.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.3 Contenido, optimizacin y moderacin de la comunidad virtual

    p Para el control de contenido, la optimizacin de recursos y la moderacin de la comunidad virtual, la entidad mediante su <em>community manager</em> puede implementar estrategias apoyadas en programas que ya han probado su eficiencia y eficacia en el <em>marketing</em> digital. 
    p En el mercado virtual existen diversos programas que coadyuvan la creacin, la optimizacin y la moderacin de la comunidad virtual. 
    p.mb-5 En continuidad, se nombran algunos de los programas en los que se pueden apoyar las empresas para la creacin y la optimizacin de los recursos, as como su moderacin:

    .bloque-texto-g.bloque-texto-g--inverso.color-primario.p-3.p-sm-4.p-md-5.mb-5
