    p.mb-5 Las mtricas en Instagram son accesibles exclusivamente a travs de dispositivos mviles, brindando estadsticas fundamentales para evaluar el desempeo de la marca en la plataforma. Estas mtricas, detalladas en la siguiente tabla, se centran en las estadsticas generales del contenido publicado:

