                td(colspan="4")(style="background-color:#F6F6F6") En la seccin de historias se pueden aadir <em>sticker</em> para que los seguidores interacten con la publicacin, en las estadsticas de las historias se podrn ver las respuestas de los usuarios. Los tipos de <em>sticker</em> que se encuentran son:
                  ul.lista-ul.mb-0
                    li.mb-0
                      i.fas.fa-circle(style="color:#33ECE0")
