                      | De <em>hashtag.</em>
                    li.mb-0
                      i.fas.fa-circle(style="color:#33ECE0")
                      | De clima o msica. 
              tr
                td(colspan="2") <strong>Impresiones</strong>
                td(colspan="4") Esta estadstica es muy til para saber si la marca est generando contenido de valor, pues muestra la cantidad de veces que se ha visto la historia, as como tambin evidenciar cuntos usuarios han visto la historia varias veces. 
                  
              tr
                td(colspan="2")(style="background-color:#F6F6F6") <strong>Alcance</strong>
                td(colspan="4")(style="background-color:#F6F6F6") En este apartado de las estadsticas se tendr un control ms exacto sobre el alcance de la campaa o publicacin, ya que muestra la cantidad nica de perfiles que han visto la publicacin, por lo que se podr realizar una comparacin entre las primeras publicaciones y las actuales con el objetivo de determinar si se tiene un crecimiento o disminucin en el alcance de las publicaciones. 

              tr
                td(colspan="2") <strong>Siguiente</strong>
                td(colspan="4") Establece el nmero de veces que los usuarios han hecho clic para ver la siguiente historia de la marca.

              tr
                td(colspan="2")(style="background-color:#F6F6F6") <strong>Volver</strong>
                td(colspan="4")(style="background-color:#F6F6F6") Nmero de veces que los seguidores han dado clic para devolverse a ver la historia de la marca. 

              tr
                td(colspan="2") <strong>Siguiente historia</strong>
                td(colspan="4") Esta estadstica evidencia la cantidad de veces que los usuarios han dado clic para pasar a la historia de otra marca o publicacin. 
              tr
                td(colspan="2")(style="background-color:#F6F6F6") <strong>Abandonos</strong>
                td(colspan="4")(style="background-color:#F6F6F6") El nmero de veces que los seguidores han tocado la historia para abandonarla.
              tr
                td(colspan="2") <strong>Clic o enlace a la pgina</strong>
                td(colspan="4") Estadstica que se encuentra disponible solo cuando se superan los 10.000 seguidores y se evidencia el nmero de personas que han deslizado la informacin en la historia para ser dirigidos a la pgina web o enlace que se ha compartido en esta. 
